import router from "/"
import store from "../store"
// import { ElMessage } from "element-plus"
import NProgress from "nprogress" // progress bar
import "nprogress/nprogress.css" // progress bar style
import { getToken, getExpires } from "@/utils/auth" // get token from cookie
import getPageTitle from "@/utils/get-page-title"

NProgress.configure({ showSpinner: false }) // showSpinner: 

const whiteList = ["/reader", "/login", "/auth-redirect"] // no redirect whitelist 

router.beforeEach(async (to, from, next) => {
  NProgress.start() // 
  document.title = getPageTitle(to.meta.title) // 
  // token
  if (getToken()) {
    // 
    if (to.path === "/login") {
      next({ path: "/" }) // 
      NProgress.done() // router.afterEach
    } else {
      // 
      if (store.getters.roles && store.getters.roles.length > 0) {
        const expires_in = getExpires() - Math.round(new Date().getTime() / 1000) //token
        console.log(`------------${to.fullPath}-------------`) //
        console.log("Expires_In: ", expires_in) //
        // token
        if (expires_in <= 2400) {
          store
            .dispatch("user/refreshToken") // token
            .catch((error) => {
              if (error.response.status === 401) {
                if (whiteList.indexOf(to.path) !== -1) {
                  next() // 
                } else {
                  // next(`/login?redirect=${to.path}`) // 
                  // NProgress.done() // 
                  location.reload() //
                }
              }
            })
        }
        next() //404
      } else {
        try {
          // roles
          const { roles } = await store.dispatch("user/getInfo")
          // : roles !
          const accessRoutes = await store.dispatch("permission/generateRoutes", roles.split(","))
          // 
          // router.addRoutes(accessRoutes) // addRoutesaddRoute
          accessRoutes.forEach((route) => router.addRoute(route))
          // addRoutes hack
          // set the replace: true, so the navigation will not leave a history record
          next({ ...to, replace: true })
        } catch (error) {
          //  token 
          await store.dispatch("user/resetToken")
          // ElMessage.error(error || "Has Error") // response
          next(`/login?redirect=${to.path}`)
          NProgress.done() // 
        }
      }
    }
    // token
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next() // 
    } else {
      next(`/login?redirect=${to.path}`) // 
      NProgress.done() // 
    }
  }
})

router.afterEach(() => {
  NProgress.done() // 
})
