import { login, register, logout, getInfo, refreshToken } from "@/api/user"
import {
  getToken,
  setToken,
  removeToken,
  getExpires,
  setExpires,
  removeExpires,
} from "@/utils/auth"
import router, { resetRouter } from "@/router"

const state = {
  token: getToken(),
  expires: getExpires(),
  name: "",
  avatar: "",
  introduction: "",
  roles: [],
}

const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_EXPIRES: (state, expires) => {
    state.expires = expires
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
}

const actions = {
  // user login
  login({ commit }, userInfo) {
    const { username, password, captcha } = userInfo
    return new Promise((resolve, reject) => {
      login({ username: username.trim(), password: password, captcha: captcha }) //Ajax
        .then((response) => {
          const { data } = response
          console.log(":", data) //
          // token
          commit("SET_TOKEN", data.access_token) //tokenstate
          commit("SET_EXPIRES", data.expires_in) //expiresstate
          setToken(data.access_token) //tokencookie
          setExpires(data.expires_in) //expirescookie
          resolve()
        })
        .catch((error) => {
          console.log(":", error) //
          reject(error)
        })
    })
  },

  // user register
  register({ commit }, userInfo) {
    const { username, password, captcha } = userInfo
    return new Promise((resolve, reject) => {
      register({ username: username.trim(), password: password, captcha: captcha }) //Ajax
        .then((response) => {
          const { data } = response
          console.log(":", data) //
          resolve()
        })
        .catch((error) => {
          console.log(":", error) //
          reject(error)
        })
    })
  },

  // get user info
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo() //Ajax
        .then((response) => {
          const { data } = response
          if (!data) {
            reject("Verification failed, please Login again.")
          }
          // 
          const { roles, userName, portrait, introduction } = data
          const rolesArr = roles.split(",") // : roles !
          // 
          if (!rolesArr || rolesArr.length <= 0) {
            reject("getInfo: roles must be a non-null array!")
          }
          commit("SET_ROLES", rolesArr)
          commit("SET_NAME", userName)
          commit("SET_AVATAR", portrait)
          commit("SET_INTRODUCTION", introduction)
          resolve(data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  refreshToken({ commit, state }) {
    return new Promise((resolve, reject) => {
      refreshToken() //Ajax
        .then((response) => {
          const { data } = response
          console.log("Token:", data) //
          // token
          commit("SET_TOKEN", data.access_token) //tokenstate
          commit("SET_EXPIRES", data.expires_in) //expiresstate
          setToken(data.access_token) //tokencookie
          setExpires(data.expires_in) //expirescookie
          resolve()
        })
        .catch((error) => {
          console.log("Token:", error) //
          reject(error)
        })
    })
  },

  // user logout
  logout({ commit, state, dispatch }) {
    return new Promise((resolve, reject) => {
      logout() //Ajax
        .then(() => {
          dispatch("resetToken")
          resetRouter() //
          dispatch("tagsView/delAllViews", null, { root: true }) // visitedViewscachedViews
          resolve()
        })
        .catch((error) => {
          dispatch("resetToken")
          resetRouter() //
          dispatch("tagsView/delAllViews", null, { root: true }) // visitedViewscachedViews
          reject(error)
        })
    })
  },

  // remove token
  resetToken({ commit }) {
    return new Promise((resolve) => {
      commit("SET_TOKEN", "")
      commit("SET_EXPIRES", "")
      commit("SET_ROLES", [])
      removeToken()
      removeExpires()
      resolve()
    })
  },

  // dynamically modify permissions
  async changeRoles({ commit, dispatch }, role) {
    const token = role + "-token"
    commit("SET_TOKEN", token)
    setToken(token)
    const { roles } = await dispatch("getInfo")
    resetRouter()
    // generate accessible routes map based on roles
    const accessRoutes = await dispatch("permission/generateRoutes", roles, { root: true })
    // dynamically add accessible routes
    // router.addRoutes(accessRoutes) // addRoutesaddRoute
    accessRoutes.forEach((route) => router.addRoute(route))
    // reset visited views and cached views
    dispatch("tagsView/delAllViews", null, { root: true })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
}
