import { createApp } from "vue"

import Cookies from "js-cookie"

import "normalize.css/normalize.css" // a modern alternative to CSS resets

import ElementPlus from "element-plus"
import * as ElementPlusIconsVue from "@element-plus/icons-vue" // ElementPlus
import "element-plus/dist/index.css" // ElementPlus
import zhLocale from "element-plus/lib/locale/lang/zh-cn" // ElementPlus
// import "element-plus/theme-chalk/dark/css-vars.css" // 

import "./styles/element-variables.scss" // 
import "@/styles/index.scss" // 

import App from "./App.vue"
import store from "./store"
import router from "./router"
import "./router/permission" // storerouter

import useSvgIcon from "./icons"
import useErrorLog from "./utils/error-log"

import "./fonts/font.css" // 

const app = createApp(App) // 

useSvgIcon(app) // SVG

useErrorLog(app) // ErrorLog

//ElementPlus
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component) //ElementPlus
}

app.use(ElementPlus, {
  size: Cookies.get("size") || "default", // set default size
  locale: zhLocale,
})
// zhLocale.el.pagination = {
//   goto: "",
//   pagesize: "/",
//   total: ` {total} `,
//   pageClassifier: "",
// }

app.use(router)
app.use(store)
app.mount("#app")
